import express, { Express, NextFunction, Request, Response } from 'express';
import bodyParser from 'body-parser';

import { CONFIG_PORT } from './utils/config';
import { AddTransaction, Ledger } from './services/ledger';
import { error } from 'console';
import { CustomError } from './utils/errors';

const app: Express = express();
app.use(bodyParser.json());
app.use((_req, res, next) => {
  res.setHeader('Content-Type', 'application/json');
  next();
});
app.use((err: Error, req: Request, res: Response, next: NextFunction) => {
  if (err instanceof CustomError) {
    res.statusCode = err.status;
    res.send(
      JSON.stringify({
        message: err.message,
        errorCode: err.errorCode,
      }),
    );
  } else {
    error(err);
    res.statusCode = 500;
    res.send(
      JSON.stringify({
        message:
          'Unexpected error occurred:' +
          (err instanceof Error ? err.message : ''),
        errorCode: 3000,
      }),
    );
  }

  next();
});

app.post('/transaction', async (req: Request, res: Response) => {
  const transaction = req.body as AddTransaction;
  const ledger = new Ledger({ accountId: transaction.accountId });

  const newTransaction = await ledger.addTransactionWithRetries(
    10,
    5,
    transaction,
  );
  res.statusCode = 200;
  res.send(JSON.stringify(newTransaction));
});

// Healthcheck endpoint
app.get('/ping', (req: Request, res: Response) => {
  res.send('pong');
});

app.get('/transactions/account/:id', async (req: Request, res: Response) => {
  const accountId = req.params.id
  const ledger = new Ledger({ accountId: accountId });
  try {
    const transactionList = await ledger.getAllTransactions();
    res.statusCode = 200;
    res.send(transactionList);
  } catch (e) {
    if (e instanceof CustomError) {
      res.statusCode = 400;
      res.send(
        JSON.stringify({
          message: e.message,
          errorCode: e.errorCode,
        }),
      );
    } else {
      error(e);
      res.statusCode = 500;
      res.send(
        JSON.stringify({
          message: 'Unexpected error occurred',
          errorCode: 3000,
        }),
      );
    }
  }
});

app.listen(CONFIG_PORT, () => {
  console.log(
    `⚡️[server]: Server is running at http://localhost:${CONFIG_PORT}`,
  );
});
