import { getDynamoDBClient } from '../utils/getDynamoDBClient';
import {
  PutItemCommand,
  QueryCommand,
  DynamoDBClient,
  ConditionalCheckFailedException,
} from '@aws-sdk/client-dynamodb';
import { marshall, unmarshall } from '@aws-sdk/util-dynamodb';
import { CONFIG_DYNAMODB_TABLE } from '../utils/config';
import {
  CannotAcquireNextTransactions,
  ErrorLimitReached,
} from '../utils/errors';

type Options = {
  accountId: string;
};

export type AddTransaction = {
  accountId: string;
  amount: number;
  description: string;
  reference: string;
  limit?: number;
};

export type Transaction = {
  id: number;
  amount: number;
  description: string;
  reference: string;
  createdAt: number;
  currentBalance: number;
};

export class Ledger {
  private accountId: string;
  private client: DynamoDBClient;

  constructor({ accountId }: Options) {
    this.accountId = accountId;
    this.client = getDynamoDBClient();
  }

  private getTransactionKey = () => `transaction:${this.accountId}`;

  getLastTransaction = async (): Promise<Transaction | null> => {
    const command = new QueryCommand({
      TableName: CONFIG_DYNAMODB_TABLE, // Replace with your DynamoDB table name
      KeyConditionExpression: `accountId = :accountIdValue`,
      ExpressionAttributeValues: {
        ':accountIdValue': { S: this.getTransactionKey() }, // Replace with the value of the accountID you want to query
      },
      ScanIndexForward: false,
      Limit: 1,
    });

    const result = await this.client.send(command);
    if (!result.Items || result.Items.length === 0) {
      return null;
    }

    return unmarshall(result.Items[0]) as Transaction;
  };

  addTransaction = async (transaction: AddTransaction) => {
    const lastTransaction = await this.getLastTransaction();
    const nextId = (lastTransaction?.id || 0) + 1;
    const nextBalance =
      (lastTransaction?.currentBalance || 0) + transaction.amount;

    if (
      typeof transaction.limit !== 'undefined' &&
      nextBalance < transaction.limit
    ) {
      throw new ErrorLimitReached();
    }

    const itemData = {
      ...transaction,
      limit: transaction.limit,
      accountId: this.getTransactionKey(),
      id: nextId,
      amount: transaction.amount,
      currentBalance: nextBalance,
      createdAt: Date.now(),
    };

    const command = new PutItemCommand({
      TableName: CONFIG_DYNAMODB_TABLE,
      Item: marshall(itemData, { removeUndefinedValues: true }),
      ConditionExpression: 'attribute_not_exists(accountId)',
    });

    await this.client.send(command);
    return {
      ...itemData,
      accountId: undefined,
    };
  };

  addTransactionWithRetries = async (
    maxRetries: number,
    baseDelayMs: number,
    transaction: AddTransaction,
  ) => {
    let retries = 0;
    let delay = baseDelayMs;

    const errors: Error[] = [];
    while (retries < maxRetries) {
      try {
        const result = await this.addTransaction(transaction);
        return result;
      } catch (error) {
        if (error instanceof ErrorLimitReached) {
          throw error;
        }
        errors.push(error as Error);

        retries++;
        await new Promise(resolve => setTimeout(resolve, delay));
        delay *= 2; // Exponential backoff
      }
    }

    const errorMessages = errors
      .map(error => (error instanceof Error ? error.message : ''))
      .join('; ');
    const errorMessage = `Transaction failed after ${maxRetries} retries: ${errorMessages}`;

    if (
      errors.every(error => error instanceof ConditionalCheckFailedException)
    ) {
      throw new CannotAcquireNextTransactions(errorMessage);
    } else {
      throw errorMessage;
    }
  };

  getAllTransactions = async () => {
    const command = new QueryCommand({
      TableName: CONFIG_DYNAMODB_TABLE,
      KeyConditionExpression: 'accountId = :a1',
      ExpressionAttributeValues: {
        ':a1': { S: this.getTransactionKey() },
      },
    });
    const res = await this.client.send(command);
    return res.Items!.map(item => unmarshall(item));
  };
}
