export abstract class CustomError extends Error {
  public abstract errorCode: number;
  public abstract status: number;
}

export class ErrorLimitReached extends CustomError {
  public errorCode = 3005;
  public status = 400;
  constructor() {
    super();
    this.message = 'Cannot create transaction because the limit was reached';
    this.name = 'ErrorLimitReached';
  }
}

export class CannotAcquireNextTransactions extends CustomError {
  public errorCode = 3006;
  public status = 429;
  constructor(details: string) {
    super();
    this.message =
      'Next transaction cannot be acquired because of too many other requests: ' +
      details;
    this.name = 'CannotAcquireNextTransactions';
  }
}

